import { Constructor } from '@helix/platform/utils';
import { IAssignmentExpression } from '@helix/platform/shared/api';

import { IRecordServerActionInputMapModel, IRecordServerActionService } from '../record-server-action.types';
import { IGetRecordServerActionService } from './get-record-server-action.types';

export function RxGetRecordServerActionServiceMixin<TBase extends Constructor<IRecordServerActionService>>(
  Base: TBase
) {
  return class RxGetRecordServerActionService extends Base implements IGetRecordServerActionService {
    constructor(...args: any[]) {
      super(...args);
    }

    getDefinitionInputMapParam(inputParamName: string, inputParamValue: string): IAssignmentExpression {
      return super.getRecordDefinitionInputMapParam(inputParamName, inputParamValue);
    }

    // TODO-VS: update definition type to use "IServiceTaskDefinition | ICustomRuleAction"
    getInputMapFromDefinition(definition: any): IRecordServerActionInputMapModel {
      return super.getRecordInputMapModel(definition);
    }
  };
}
